import App from '../App'

const bindphone = r => require.ensure([], () => r(require('../page/validate/bindphone')), 'bindphone')
const list = r => require.ensure([], () => r(require('../page/validate/result')), 'list')

export default [{
    path: '/',
    component: App, //顶层路由，对应index.html
    children:[ //二级路由。对应App.vue
        //地址为空时跳转home页面
        {
            path: '',
            redirect: '/validate'
        },
        //绑定手机号
        {
            path:'/validate',
            component:bindphone,
            meta:{
                title:'绑定手机号'
            }
        },
         //查询
        {
            path:'/list',
            component:list,
            meta:{
                title:'列表'
            }
        }
    ]
}]



